nav li #btn{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 1.4rem;
    width: 80%;
    padding-top: 5%;
    margin-bottom: 10%;
    padding-bottom: 5%;
    border-radius: 7px;
    background-color: whitesmoke;
    text-decoration: none;
    box-shadow:
            0px 1px 2px rgba(0,0,0,0.1), 
            0px 2px 4px rgba(0,0,0,0.1), 
            0px 4px 8px rgba(0,0,0,0.1), 
            0px 8px 16px rgba(0,0,0,0.1);

}


nav li {
    list-style-type: none;
}
