#logo{
    aspect-ratio: 1/1;
    height: 40px;
    padding-left: 5px;
}

hr{
    opacity: 35%;
    margin-top: -10px;
}

section{
    max-height: 80%;
    padding: 3%;
    margin-right: 3%;
    border-radius: 7px;
    background-color: whitesmoke;
    box-shadow:
            0px 1px 2px rgba(0,0,0,0.1), 
            0px 2px 4px rgba(0,0,0,0.1), 
            0px 4px 8px rgba(0,0,0,0.1), 
            0px 8px 16px rgba(0,0,0,0.1);

}

#technologies{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}