#animationContainer{
    display:grid;
    grid-column-start: 1;
    grid-column-end: 3;
    height: 100%;
}

.animation {
    opacity: 15%;
    height: 100%;
    position: absolute;
    width: 100%;

    opacity: 15%;
    transition: opacity 1s ease-in-out;

}

.animation:nth-child(1){
    background-image: url("../../Assets/Waves/Wave8983AA.svg");
    animation: wave 15s ease-in-out;
    z-index: 1;
}
.animation:nth-child(2){
    background-image: url("../../Assets/Waves/WaveBBC5C9.svg");
    animation: wave 18s ease-in-out;
    z-index: 2;
}
.animation:nth-child(3){
    background-image: url("../../Assets/Waves/WaveF58990.svg");
    animation: wave 21s ease-in-out;
    z-index: 3;
}
.animation:nth-child(4){
    background-image: url("../../Assets/Waves/WaveF5F1ED.svg");
    animation: wave 25s ease-in-out;
    z-index: 4;
}

.fade-out {
    opacity: 15%;
    transition: opacity 1s;
  }
  
  .fade-out.fade-out-active {
    opacity: 0;
  }
  
  .fade-in {
    opacity: 0;
    transition: opacity 1s;
  }
  
  .fade-in.fade-in-active {
    opacity: 15%;
  }  
  

@keyframes wave {
    0% {
        background-position: 0% 0;
      }
    50% {
        background-position: -100% 0;
      }
    100%{
        background-position: 0 0;
    }
}

